@import url(https://fonts.googleapis.com/css?family=Roboto:500);
.App {
  text-align: center;
  padding: 2% 5%;
}

.App * {
  box-sizing: border-box;
}

ul {
  padding: 0;
  list-style: none;
}

li {
  background-color: rgb(223, 245, 252);
  border: 1px solid black;
  margin-bottom: 10px;
}

#button-details {
  margin-bottom: 15px;
  margin-top: 10px;
}

p {
  margin: 5px 0;
}

#city-search-title {
  margin-top: 10px;
  margin-bottom: 10px;
}

#number-of-events-title {
  margin-top: 0px;
  margin-bottom: 10px;
}

input[type="text"],
input[type="number"] {
  padding: 8px 6px 8px 10px;
  border-radius: 4px;
  outline: none;
  border: 1px solid #000000;
  font-size: 14px;
  line-height: 20px;
  align-self: center;
  height: 38px;
  margin-bottom: 20px;
}

.city {
  width: 250px;
  text-align: center; 
}

#city-search {
  display: flex;
  flex-direction: column;
  margin: auto;
  position: relative;
  background-color: rgb(177, 232, 248);
  border-top: 2px solid #000000;
  border-left: 2px solid #000000;
  border-right: 2px solid #000000;
}

#number-events {
  background-color: rgb(177, 232, 248);
  border-bottom: 2px solid #000000;
  border-left: 2px solid #000000;
  border-right: 2px solid #000000;
}

.numberEvents {
  text-align: center;
  margin-top: 8px; 
}

.suggestions {
  width: 250px;
  top: 45px;
  z-index: 2;
  align-self: center;
  margin: 0;
  font-size: 14px;
}

.suggestions > li {
  padding: 8px 6px 8px 10px;
  background-color: #eee;
  cursor: default;
  text-align: center; 
}

.suggestions > li:last-child {
  border-bottom-left-radius: 4px;
  border-bottom-right-radius: 4px;
}

.suggestions > li:hover {
  background-color: #bcdcdb;
}

.event {
  padding: 10px 10px 35px;
  margin-bottom: 10px;
  text-align: left;
  border: 1px solid #ccc;
  border-radius: 4px;
  position: relative;
}

.details-btn {
  position: absolute;
  right: 5px;
  bottom: 5px;
  padding: 5px 8px;
  border-radius: 4px;
  outline: none;
  cursor: pointer;
}

#event-description {
  margin-left: 100px;
  margin-right: 100px;
  text-align: justify;
}

@media (max-width: 599.99px) {
  #event-description {
    margin-left: 80px;
    margin-right: 80px;
    text-align: justify;
  }
}

@media (max-width: 499.99px) {
  #event-description {
    margin-left: 60px;
    margin-right: 60px;
    text-align: justify;
  }
}

@media (max-width: 399.99px) {
  #event-description {
    margin-left: 30px;
    margin-right: 30px;
    text-align: justify;
  }
}

@media (max-width: 299.99px) {
  #event-description {
    margin-left: 10px;
    margin-right: 10px;
    text-align: justify;
  }

  .city {
    width: 160px;
    text-align: center; 
  }

  .suggestions {
    width: 160px;
    top: 45px;
    z-index: 2;
    align-self: center;
    margin: 0;
    font-size: 10px;
  }

  .numberEvents {
    width: 160px;
    text-align: center; 
  }

}